// toast组件，拿到的不是组件，是方法；
// 如此这般：Toast('我是提示文案，建议不超过十五字~');
import Toast from '../../wxcomponents/weapp/dist/toast/toast'
export default {
	data() {
		return {
			ttCont: '',
			// 问题类型
			pmTyep: [
				'使用体验',
				'使用体验',
				'使用体验',
				'使用体验',
				'使用体验'
			],
			pmTyepCut: 0,
			
			// 小彩蛋次数
			cai: 0,
			caiflag: false
		}
	},
	methods: {
		// 点击提交按钮
		subFeedBack() {
			let res = this.ttCont.trim()
			console.log(res.length)
			// 判断输入框内容
			if(!res.length) {
				Toast('请输入内容')
				this.ttCont = ''
				this.cai += 1
				if(this.cai>=5) {
					Toast('怎么肥四，小老弟？？')
					this.caiflag = true
					this.cai = 0
					setTimeout(()=>{
						this.caiflag = false
					},3000)
				}
			} else {
				console.log('当前问题类型 => ' + this.pmTyep[this.pmTyepCut])
				console.log('当前内容 => ' + res)
				console.log('发送http请求')
				
				// 提交成功,清空内容
				Toast('感谢您的反馈~！')
				this.ttCont = ''
				this.pmTyepCut = 0
			}
		},
		// 选择问题类型
		pmTypeSel(idx) {
			this.pmTyepCut = idx
		},
		
		// 菜单关闭
		caiOnClose() {
			this.caiflag = false
			this.cai = 0
		}
	}
}
